import React from 'react';
import { motion } from 'framer-motion';
import { FaChalkboardTeacher, FaUserGraduate, FaDoorOpen, FaCalendarAlt } from 'react-icons/fa';

const StatCards = ({ totalClasses, activeLecturers, totalRooms, totalTimetables }) => {
  const stats = [
    {
      title: 'Total Classes',
      value: totalClasses,
      icon: <FaChalkboardTeacher className="text-blue-500" />,
      color: 'bg-blue-100',
      textColor: 'text-blue-600',
      borderColor: 'border-blue-300'
    },
    {
      title: 'Active Lecturers',
      value: activeLecturers,
      icon: <FaUserGraduate className="text-green-500" />,
      color: 'bg-green-100',
      textColor: 'text-green-600',
      borderColor: 'border-green-300'
    },
    {
      title: 'Total Rooms',
      value: totalRooms,
      icon: <FaDoorOpen className="text-purple-500" />,
      color: 'bg-purple-100',
      textColor: 'text-purple-600',
      borderColor: 'border-purple-300'
    },
    {
      title: 'Total Timetables',
      value: totalTimetables,
      icon: <FaCalendarAlt className="text-orange-500" />,
      color: 'bg-orange-100',
      textColor: 'text-orange-600',
      borderColor: 'border-orange-300'
    }
  ];

  return (
    <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
      {stats.map((stat, index) => (
        <motion.div
          key={stat.title}
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: index * 0.1, duration: 0.3 }}
          whileHover={{ y: -5, boxShadow: '0 10px 25px -5px rgba(0, 0, 0, 0.1)' }}
          className={`relative overflow-hidden rounded-lg border ${stat.borderColor} p-6 ${stat.color} shadow-sm`}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500">{stat.title}</p>
              <motion.p 
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.3 + index * 0.1 }}
                className={`mt-2 text-3xl font-bold ${stat.textColor}`}
              >
                {stat.value}
              </motion.p>
            </div>
            <div className="rounded-full p-3 text-2xl">{stat.icon}</div>
          </div>
          <div className="absolute bottom-0 left-0 h-1 w-full bg-gradient-to-r from-transparent via-white to-transparent opacity-30"></div>
        </motion.div>
      ))}
    </div>
  );
};

export default StatCards;
