import React from 'react';
import { motion } from 'framer-motion';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

const RoomUtilization = () => {
  // Mock data - replace with real data when available
  const utilizationData = {
    labels: ['In Use', 'Available', 'Maintenance'],
    datasets: [
      {
        data: [65, 25, 10],
        backgroundColor: ['#4F46E5', '#10B981', '#F59E0B'],
        borderColor: ['#4338CA', '#059669', '#D97706'],
        borderWidth: 1,
        hoverOffset: 4,
      },
    ],
  };

  const options = {
    plugins: {
      legend: {
        position: 'bottom',
        labels: {
          usePointStyle: true,
          padding: 20,
          font: {
            size: 12
          }
        }
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            return `${context.label}: ${context.raw}%`;
          }
        }
      }
    },
    cutout: '70%',
    maintainAspectRatio: false,
  };

  // Top rooms by utilization
  const topRooms = [
    { name: 'Room 101', utilization: 95 },
    { name: 'Computer Lab', utilization: 89 },
    { name: 'Theater Hall', utilization: 76 },
    { name: 'Library', utilization: 62 },
  ];

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.95 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.3 }}
      className="rounded-lg bg-white p-6 shadow-md"
    >
      <h2 className="mb-6 text-xl font-bold text-gray-800">Room Utilization</h2>
      
      <div className="relative h-64 w-full">
        <Doughnut data={utilizationData} options={options} />
        <div className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 transform text-center">
          <p className="text-3xl font-bold text-gray-800">65%</p>
          <p className="text-sm text-gray-500">Utilization</p>
        </div>
      </div>
      
      <div className="mt-6">
        <h3 className="mb-3 text-sm font-medium text-gray-500">Top Utilized Rooms</h3>
        <div className="space-y-3">
          {topRooms.map((room, index) => (
            <motion.div 
              key={room.name}
              initial={{ x: -10, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              transition={{ delay: index * 0.1 }}
              className="flex items-center justify-between"
            >
              <span className="text-sm font-medium text-gray-700">{room.name}</span>
              <div className="flex w-32 items-center">
                <div className="relative h-2 w-full rounded-full bg-gray-200">
                  <motion.div
                    initial={{ width: 0 }}
                    animate={{ width: `${room.utilization}%` }}
                    transition={{ duration: 1, delay: 0.5 + index * 0.1 }}
                    className="absolute left-0 top-0 h-2 rounded-full bg-indigo-500"
                  ></motion.div>
                </div>
                <span className="ml-2 text-xs font-medium text-gray-600">{room.utilization}%</span>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default RoomUtilization;
