import React, { useEffect, useState } from 'react';
import StatCards from "./StatCards";
import Schedules from "./Schedules";
import RoomUtilization from "./RoomUtilization";
import { 
  fetchTodaysSchedules, 
  fetchTotalClasses, 
  fetchActiveLecturers, 
  fetchTotalRooms, 
  fetchTotalTimetables 
} from "../../utils/Services/dashboardService";

const DashboardPage = () => {
  const [schedules, setSchedules] = useState([]);
  const [totalClasses, setTotalClasses] = useState(0);
  const [activeLecturers, setActiveLecturers] = useState(0);
  const [totalRooms, setTotalRooms] = useState(0);
  const [totalTimetables, setTotalTimetables] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      const [classes, lecturers, rooms, timetables, schedulesData] = await Promise.all([
        fetchTotalClasses(),
        fetchActiveLecturers(),
        fetchTotalRooms(),
        fetchTotalTimetables(),
        fetchTodaysSchedules()
      ]);

      setTotalClasses(classes);
      setActiveLecturers(lecturers);
      setTotalRooms(rooms);
      setTotalTimetables(timetables);
      setSchedules(schedulesData);
    };

    fetchData();
  }, []);

  return (
    <div className="grid grid-cols-1 gap-6 p-4 lg:grid-cols-4">
      {/* StatCards spans the full width */}
      <div className="col-span-1 lg:col-span-4">
        <StatCards 
          totalClasses={totalClasses} 
          activeLecturers={activeLecturers} 
          totalRooms={totalRooms} 
          totalTimetables={totalTimetables} 
        />
      </div>

      {/* Schedules spans 2 columns */}
      <div className="col-span-1 lg:col-span-2">
        <Schedules schedules={schedules} />
      </div>

      {/* RoomUtilization spans 2 columns */}
      <div className="col-span-1 lg:col-span-2">
        <RoomUtilization />
      </div>
    </div>
  );
};

export default DashboardPage;