import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FaClock, FaChalkboardTeacher, FaMapMarkerAlt, FaRegCalendarAlt } from 'react-icons/fa';

const Schedules = ({ schedules }) => {
  const [filter, setFilter] = useState('all');
  
  const filterOptions = [
    { value: 'all', label: 'All' },
    { value: 'morning', label: 'Morning' },
    { value: 'afternoon', label: 'Afternoon' },
    { value: 'evening', label: 'Evening' }
  ];

  // Filter schedules based on selected filter
  const filteredSchedules = schedules.filter(schedule => {
    if (filter === 'all') return true;
    const hour = parseInt(schedule.startTime.split(':')[0]);
    if (filter === 'morning' && hour < 12) return true;
    if (filter === 'afternoon' && hour >= 12 && hour < 17) return true;
    if (filter === 'evening' && hour >= 17) return true;
    return false;
  });

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="rounded-lg bg-white p-6 shadow-md"
    >
      <div className="mb-6 flex items-center justify-between">
        <h2 className="text-xl font-bold text-gray-800">Today's Schedule</h2>
        <div className="flex space-x-2 rounded-lg bg-gray-100 p-1">
          {filterOptions.map(option => (
            <button
              key={option.value}
              onClick={() => setFilter(option.value)}
              className={`rounded-md px-3 py-1 text-sm font-medium transition-all ${
                filter === option.value
                  ? 'bg-blue-500 text-white shadow-sm'
                  : 'text-gray-500 hover:bg-gray-200'
              }`}
            >
              {option.label}
            </button>
          ))}
        </div>
      </div>

      <div className="space-y-4">
        <AnimatePresence>
          {filteredSchedules.length > 0 ? (
            filteredSchedules.map((schedule, index) => (
              <motion.div
                key={schedule.id || index}
                initial={{ x: -20, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: 20, opacity: 0 }}
                transition={{ delay: index * 0.05 }}
                className="relative overflow-hidden rounded-lg border-l-4 border-blue-500 bg-white p-4 shadow-sm hover:shadow-md"
              >
                <div className="flex flex-col md:flex-row md:items-center md:justify-between">
                  <div className="mb-2 md:mb-0">
                    <h3 className="text-lg font-semibold text-gray-800">{schedule.className || schedule.class}</h3>
                    <div className="mt-1 flex items-center text-sm text-gray-600">
                      <FaChalkboardTeacher className="mr-1" />
                      <span>{schedule.lecturer}</span>
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-4">
                    <div className="flex items-center text-sm text-gray-500">
                      <FaClock className="mr-1" />
                      <span>{`${schedule.startTime} - ${schedule.endTime}`}</span>
                    </div>
                    
                    <div className="flex items-center text-sm text-gray-500">
                      <FaMapMarkerAlt className="mr-1" />
                      <span>{schedule.room}</span>
                    </div>
                  </div>
                </div>
                
                <div className="mt-2 flex items-center text-xs text-gray-400">
                  <FaRegCalendarAlt className="mr-1" />
                  <span>{new Date().toLocaleDateString()}</span>
                </div>
                
                {/* Progress indicator */}
                <div className="mt-3 h-1 w-full rounded-full bg-gray-100">
                  <motion.div
                    initial={{ width: 0 }}
                    animate={{ width: '60%' }}
                    transition={{ delay: 0.5, duration: 1 }}
                    className="h-full rounded-full bg-blue-500"
                  ></motion.div>
                </div>
              </motion.div>
            ))
          ) : (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              className="rounded-lg bg-gray-50 p-6 text-center"
            >
              <p className="text-gray-500">No schedules found for the selected time.</p>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </motion.div>
  );
};

export default Schedules;
